.size-wrapper {
  position: relative;
  width: 40px;
  height: 40px;
  cursor: pointer;
  transition: all 0.3s;
  border-radius: 5px;
}

.size-wrapper:hover{
  box-shadow: inset 0 0 20px  rgba(0, 0, 0, .5);
}

.size-wrapper:active {
  background-color: #499b15;
}

.size-image {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  background: url('../imgs/resize-square.png') no-repeat;
  width: 24px;
  height: 24px;
  background-color: #fff;
  cursor: pointer;
}
